*
{
    margin: 0;padding: 0;
}
.home_title
{
    text-align: center;
    color: black;
    height: 5%;
}
.home{
    max-width: 1000px;
    margin: 0 auto;
    margin-top: 180px;
    height: 50%;
}
.home_container{
    height: 68vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.home_formcontainer{
    box-shadow: 1px 4px 12px rgba(0,0,0,4.3);
    background-color: rgb(7, 64, 85);
    padding: 20px;
    border-radius: 10px;
    width: 400px;
    
}

.home_formcontainer input{
    border: 1px solid white;
    outline: none;
    padding: 15px 10px;
    width: 80%;
    border-radius: 8px;
    margin: 10px 10px;
    background: transparent;
    color: black;
    font-size: 15px;
    font-weight: bold;
}
.home_formcontainer button{
    background-color: transparent;
    border: 1px solid white;
    outline: none;
    font-size: 18px;
    font-weight:400px;
    color: white;
    padding: 10px;
    width: 100px;
    margin-top: 20px;
    border-radius: 12px;
    cursor: pointer;
    margin-left: 110px;
    background-color: rgb(4, 15, 27);
}
.home_formcontainer input::placeholder{
    color: white;
}
