*{
    margin: 0;padding: 0;
}
h1{
    font-size:x-large;
    color: white;
    padding: 10px;
}
nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: rgb(4, 15, 27);
    padding: 10px 0;
}
nav ul{
    display: flex;
    justify-content: space-between;
    width: 40%;
    align-items: center;
    margin-right: 20px;
    text-decoration-color: white;
}
nav ul a{
    font-size: 20px;
    font-weight: 400;
    color: white;
    text-decoration: none;
}
nav ul a:hover{
    color: rgb(179, 6, 6);
}